@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  min-height: 100vh;
}

header.header {
  text-align: center;
  padding: 20px 40px 60px 40px;
}

h1.title {
  margin: 40px 0px 20px 0px;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 4px;
  font-size: 40px;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.recipe {
  border-radius: 10px;
  box-shadow: 8px 5px 5px 2px #9e9c9c26;
  margin: 20px;
  padding: 20px 0;
  background: whitesmoke;
  width: 400px;
  text-align: center;
}

.image {
  width: 100%;
  overflow: hidden;
  margin: 0 auto;
}

.image img {
  display: block;
  width: 100%;
  margin: 0;
}

.title {
  font-family: 'Roboto', sans-serif;
  font-size: 1.6rem;
  margin: 15px 0 0 0;
  letter-spacing: 4px;
}

.ingredients-div {
  text-align: left;
}

.ingredients-div p {
  padding: 20px 0 0 20px;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 2px;
  font-family: 'Roboto', sans-serif;
}

.ingredients {
  list-style-type: none;
  padding: 10px 20px;
  letter-spacing: 1px;
}

.ingredient {
  margin: 10px 0;
}

.ingredients i {
  color: red;
}

.search-bar {
  max-width: 320px;
  min-width: 220px;
  width: 50%;
  border: none;
  padding: 12px;
  box-shadow: 0 0 5px 0 #00000040;
  border-radius: 20px;
  margin: 10px;
  box-shadow: 0 0 5px 0 #00000040;
}

.search-button {
  background: lightcoral;
  border: none;
  padding: 12px 20px;
  color: white;
  border-radius: 5px;
}

.search-bar:focus,
.search-bar:hover {
  box-shadow: 0 0 5px 2px #00000040;
  outline: none;
}

.search-button:focus {
  box-shadow: 0 0 2px 2px #00000040;
  outline: none;
}

